export default {
    namespaced: true,
    state: {
        case: {
            administrativeCase: 119,
            criminalCase: 327,
            otherCase: 78
        },
        caseType: {
            robbery: 100,
            fraud: 19,
            toRob: 227,
            theft: 100,
            toPick: 78
        },
        caseArea: {
            ch: 55,
            gx: 23,
            jj: 127,
            jn: 200,
            qy: 59,
            wh: 60
        },
        caseRelated: {
            administrativeCase: 119,
            criminalCase: 327,
            otherCase: 78
        }
    },
    mutations: {
        addCase(state, val) {
            switch (val) {
                case 0:
                    state.case.administrativeCase += 1;
                    break;
                case 1:
                    state.case.criminalCase += 1;
                    break;
                case 2:
                    state.case.otherCase += 1;
                    break;
            }
        },
        addCaseType(state, val) {
            switch (val) {
                case 0:
                    state.caseType.robbery += 1;
                    break;
                case 1:
                    state.caseType.fraud += 1;
                    break;
                case 2:
                    state.caseType.toRob += 1;
                    break;
                case 3:
                    state.caseType.theft += 1;
                    break;
                case 4:
                    state.caseType.toPick += 1;
                    break;
            }
        },
        addArea(state, val) {
            switch (val) {
                case 0:
                    state.caseArea.ch += 1;
                    break;
                case 1:
                    state.caseArea.gx += 1;
                    break;
                case 2:
                    state.caseArea.jj += 1;
                    break;
                case 3:
                    state.caseArea.jn += 1;
                    break;
                case 4:
                    state.caseArea.qy += 1;
                    break;
                case 5:
                    state.caseArea.wh += 1;
                    break;
            }


        }
    },
    actions: {
        asynAddCar({ state, commit, rootState }) {
            let randomAdd = Math.round(Math.random() * 2);
            commit("addCase", randomAdd)
        },
        asynAddCarType({ state, commit, rootState }) {
            let randomAdd = Math.round(Math.random() * 4);
            commit("addCaseType", randomAdd)
        },
        asynAddArea({ state, commit, rootState }) {

            let randomAdd = Math.round(Math.random() * 5);
            commit("addArea", randomAdd)
        },
    },
    getters: {
        totalCase(state) {
            return state.case.administrativeCase + state.case.criminalCase + state.case.otherCase
        }
    }
}