export default {
    namespaced: true,
    state: {
        reportedCar: {
            // 开道
            makeDetour: 23,
            // 交警
            trafficPolice: 53,
            // 快反
            fastReflexion: 12,
            // 巡警
            patrolPolice: 36,
            // 警车
            policeCar: 13,
        },
        reportedPolice: {
            peoplePolice: 15,
            helpPolice: 35,
            assistPolice: 20

        }
    },
    mutations: {
        addReportedCar(state, val) {
            state.reportedCar.makeDetour += val;
            state.reportedCar.fastReflexion += val;
            state.reportedCar.trafficPolice += val;
            state.reportedCar.patrolPolice += val;
            state.reportedCar.policeCar += val;

        },
        addReportedPolice(state, val) {
            state.reportedPolice.peoplePolice += val;
            state.reportedPolice.helpPolice += val;
            state.reportedPolice.assistPolice += val;
        }
    },
    actions: {
        asyncReportedCar({ state, commit, rootState }) {
            commit("addReportedCar", 1)
        },
        asyncReportedPolice({ state, commit, rootState }) {
            commit("addReportedPolice", 1)

        }
    },
    getters: {}
}