export default {
    namespaced: true,
    state: {
        acceptWarning: 3,
        acceptWarningTyps: {
            xs: 1,
            za: 1,
            dl: 1,
            qt: 1,
            zh: 1,
            wf: 1,
        },
        acceptWarningArea: {
            jn: 1,
            ch: 1,
            wh: 1,
            jj: 1,
            qy: 1,
            gx: 1,
        }
    },
    mutations: {
        addAcceptWarning(state, val) {
            state.acceptWarning += val;
        },
        addWarningTypes(state, val) {
            switch (val) {
                case 0:
                    state.acceptWarningTyps.xs = state.acceptWarningTyps.xs + 1;
                    break;
                case 1:
                    state.acceptWarningTyps.za = state.acceptWarningTyps.za + 1;
                    break;
                case 2:
                    state.acceptWarningTyps.dl = state.acceptWarningTyps.dl + 1;
                    break;
                case 3:
                    state.acceptWarningTyps.qt = state.acceptWarningTyps.qt + 1;
                    break;
                case 4:
                    state.acceptWarningTyps.zh = state.acceptWarningTyps.zh + 1;
                    break;
                case 5:
                    state.acceptWarningTyps.wf = state.acceptWarningTyps.wf + 1;
                    break;
            }
        },
        addWarningArea(state, val) {
            switch (val) {
                case 0:
                    state.acceptWarningArea.jn = state.acceptWarningArea.jn + 1;
                    break;
                case 1:
                    state.acceptWarningArea.ch = state.acceptWarningArea.ch + 1;
                    break;
                case 2:
                    state.acceptWarningArea.wh = state.acceptWarningArea.wh + 1;
                    break;
                case 3:
                    state.acceptWarningArea.jj = state.acceptWarningArea.jj + 1;
                    break;
                case 4:
                    state.acceptWarningArea.qy = state.acceptWarningArea.qy + 1;
                    break;
                case 5:
                    state.acceptWarningArea.gx = state.acceptWarningArea.gx + 1;
                    break;
            }
        }
    },
    actions: {
        asyncAddAcceptWarning({ state, commit, rootState }) {
            commit("addAcceptWarning", 1)
        },
        asyncAddWarningTypes({ state, commit, rootState }) {
            let randomAdd = Math.round(Math.random() * 5);
            commit("addWarningTypes", randomAdd)
        },
        asyncAddWarningArea({ state, commit, rootState }) {
            let randomAdd = Math.round(Math.random() * 5);
            commit("addWarningArea", randomAdd)
        }
    },
    getters: {}
}